import http from '../../http';

// 获取小区数据
const getAreaList = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/app/owner/homePage/getAppAreaList.do', { data: JSON.stringify(reqData) }, true, 'homeUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}
// 获取工单标签
const getWorkLabelList = (params) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-property-repair/complLabel/listByType', params, true,'homeUrl').then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}

