<template>
    <div class="complaintReportingEvaluate">
        <!-- 头部 -->
        <navigation-bar  @onLeftClick="onLeftClick" :pageName="title" :bothWidth="headerItemWidth">
            <template v-slot:nav-right>
               <div class="rightBtn" :class="{rightBtnThemeL:bindThemeL}" @click="onRightClick">提交</div>
            </template>
        </navigation-bar>


        <section class="content">
             <!-- 是否完成 -->
            <section class="isComplate" :class="{isComplateThemeL:bindThemeL}">
                <div class="isComplateHeader" :class="{isComplateHeaderThemeL:bindThemeL}">请选择是否完成</div>
                <div class="isComContent">
                    <van-radio-group v-model="radio">
                        <van-cell-group>
                            <van-cell title="是 , 已完成" :title-style="{fontSize: titleThemeL}" clickable @click="radio = '1'">
                            <van-radio slot="right-icon" name="1" checked-color="#FB5768"/>
                            </van-cell>
                            <van-cell title="否 , 未完成" :title-style="{fontSize: titleThemeL}" clickable @click="radio = '2'">
                            <van-radio slot="right-icon" name="2" checked-color="#FB5768" />
                            </van-cell>
                        </van-cell-group>
                    </van-radio-group>
                </div>
            </section>

            <!-- 已完成工单评价 -->
            <section class="ComplateBox" v-if='radio == 1'>
                <div class="conInput" :class="{themeL:bindThemeL}">
                    <van-cell-group>
                        <van-field
                            v-model="message"
                            rows="8"
                            autosize
                            type="textarea"
                            maxlength="200"
                            placeholder="请输入评价内容..."
                        />
                    </van-cell-group>
                </div>
                <div class="lengthTotal">{{contentLength}}/200</div>
                <div class="service">
                    <div class="serviceLeft" :class="{serviceLeftThemeL:bindThemeL}">服务评分</div>
                    <div class="serviceRight">
                        <div class="serviceImg" v-for="(item,index) in xingNum" :key="index">
                            <img :src="item" @click="changeStar(index)">
                        </div>
                    </div>
                </div>
            </section>

            <!-- 未完成工单评价 -->
            <section class="NoComplateBox" v-if='radio == 2'>
                <div>
                    <div class="noWorkCon" :class="{themeL:bindThemeL}">
                        <van-cell-group>
                            <van-field
                                v-model="message"
                                rows="8"
                                autosize
                                type="textarea"
                                maxlength="200"
                                placeholder="请输入未完成原因..."
                            />
                        </van-cell-group>

                    </div>
                    <div class="lengthTotal">{{contentLength}}/200</div>
                </div>
            </section>
        </section>



    </div>
</template>

<script>
import NavigationBar from '@/common/currency/NavigationBar.vue';
import {evaluateWorkOrder} from '@/services/servers/compaintReporting'
import { Toast } from 'vant';

export default {
    name:'complaintReportingEvaluate',
    components:{
        NavigationBar,
    },
    data(){
        return{
            title:"评价",
            radio:'1',
            message:'',
            evalLeaval:5,
            complateCode:'',
            contentLength:0,
            xingImg:require('@/assets/images/common/icon_evaluate_red.png'),
            xingNoImg: require('@/assets/images/common/icon_evaluate_gray.png'),
            headerItemWidth:{
                width:'74px',
            },
            xingNum:[
                require('@/assets/images/common/icon_evaluate_red.png'),
                require('@/assets/images/common/icon_evaluate_red.png'),
                require('@/assets/images/common/icon_evaluate_red.png'),
                require('@/assets/images/common/icon_evaluate_red.png'),
                require('@/assets/images/common/icon_evaluate_red.png'),
            ],
            /*大字模式*/
            bindThemeL: this.$curTheme==0 ? false : true,
            titleThemeL: "15px"
        }
    },
    mounted(){
        this.complateCode = this.$route.query.complateCode;
    },
    watch:{
        message:{
            handler(newName, oldName) {
                this.contentLength = newName.length;
    　　    },
    　　    immediate: true
        },
        radio:{
            handler(newName, oldName) {
                this.message = '';
    　　    },
    　　    immediate: true
        }
    },
    methods:{
        onLeftClick(){
           this.$router.go(-1);
        },
        onRightClick(){
            //完成 and 驳回
            let isDone = '0';
            if(this.message.length <= 0){
                if(this.radio == 1){
                    // Toast('请输入评价内容')
                }else{
                    Toast('请输入未完成原因')
                    return
                }

            }
            if(this.radio == '1'){
                isDone = '1'
            };
            let data = {
                isDone: isDone, //结束标识 0 驳回 1 完成
                evalContg: this.message, //评价的内容
                evalLevel: this.evalLeaval, //评价的星级
                complCode: this.complateCode, //工单代码
                areaCode:this.$userInfo.AREA_CODE,
                origFlag:'0',
                ownerCode:this.$userInfo.OWNER_CODE,
                roomCode: this.$userInfo.ROOM_CODE,
                roomName:this.$userInfo.ROOM_NAME,
                evaluatorName:this.$userInfo.OWNER_NAME,
                createdBy:this.$userInfo.OWNER_CODE,
                closeType:'0'
            };
            evaluateWorkOrder(data).then(
                res => {
                    if(res.resultCode == '0'){
                        Toast(res.msg)
                        this.$router.go(-1);
                    }else{
                        Toast(res.msg)
                    }
                },
                err => {

                }
            )
        },
        changeStar(value){
            console.log(value);
            this.xingNum = [];
            this.evalLeaval = value + 1;
            let arr = [];
            for(let i = 0;i<5;i++){
                if(i <= value){
                    arr.push(this.xingImg);
                }else{
                    arr.push(this.xingNoImg);
                }
            }
            this.xingNum = arr;

        }
    }
}
</script>

<style >
.complaintReportingEvaluate{
    width: 100%;
    height: 100%;
}
.complaintReportingEvaluate .van-hairline--top-bottom::after{
    border: none
}
.complaintReportingEvaluate .van-field__control{
    height: 155px !important;
}
.complaintReportingEvaluate .themeL .van-field__control{
    font-size: 15px !important;
}
.complaintReportingEvaluate .rightBtn{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 32px;
    width: 100%;
    text-align: right;
    padding-right: 9px
}
.complaintReportingEvaluate .rightBtnThemeL{
    font-size: 17px !important;
}
.complaintReportingEvaluate .content{
    width: 100%;
    height: calc(100% - 59px);
    padding-top: 15px;
}
/* 工单是否完成 */
.complaintReportingEvaluate .isComplate{
    width: calc(100% - 70px);
    margin: 0 15px;
    padding: 20px;
    height: 113px;
    background-color: #ffffff;
    box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.50);
    border-radius: 4px;
    overflow: hidden;
}
.complaintReportingEvaluate .isComplateThemeL{
    padding: 20px 20px 0;
    height: 145px;
}
.complaintReportingEvaluate .isComplateThemeL .van-cell {
    line-height: 35px;
}
.complaintReportingEvaluate .van-hairline--top-bottom{
    border: none;
}
.complaintReportingEvaluate .isComplateHeader{
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #333333;
    letter-spacing: -0.04px;
    line-height: 20px;
}
.complaintReportingEvaluate .isComplateHeaderThemeL{
    font-size: 18px !important;
}
.complaintReportingEvaluate .isComContent{
    margin-top: 10px;
    color: #888888;
}
/* 已完成工单评价 */
.complaintReportingEvaluate .ComplateBox{
    margin: 15px;
    padding: 20px;
    width: calc(100% - 70px);
    height: 240px;
    background-color: #fff;
    box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.50);
    border-radius: 4px;
    overflow: hidden;
}
.complaintReportingEvaluate .conInput{
    width: 100%;
    height: calc(100% - 80px);
    overflow: hidden;
}
.complaintReportingEvaluate .lengthTotal{

    width: calc(100% - 5px);
    font-size: 14px;

    text-align: right;
    padding-right: 5px;
    z-index: 200;

    height:28px;
    line-height: 28px;
}
.complaintReportingEvaluate .service{
    width: 100%;
    height: 21px;
    padding-top: 20px;
    border-top: 1px solid #D3D3D3;
    display: flex;
    justify-content: space-between;
}
.complaintReportingEvaluate .serviceLeft{
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #333333;
    letter-spacing: -0.04px;
    height: 21px;
    line-height: 21px;
}
.complaintReportingEvaluate .serviceLeftThemeL{
    font-size: 18px !important;
}
.complaintReportingEvaluate .serviceImg{
    width: 18px;
    height: 18px;
    margin-right: 2px
}
.complaintReportingEvaluate .serviceImg img{
    display: block;
    width: 100%;
    height: 100%;
}
.complaintReportingEvaluate .serviceRight{
    display: flex;
}
.complaintReportingEvaluate .NoComplateBox{
    margin: 15px;
    padding: 20px;
    width: calc(100% - 70px);
    height: 178px;
    background-color: #fff;
    box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.50);
    border-radius: 4px;
    overflow: hidden;
}
.complaintReportingEvaluate .noWorkCon{
    position: relative;
    width: 100%;
    height:170px;
}
</style>
